import React, { useState, useEffect } from 'react'
import { Tooltip, Icon } from 'antd';
import Axios from 'axios'

function LikeDislikes(props) {
    const [Likes, setLikes] = useState(0)
    const [LikeAction, setLikeAction] = useState(null)
    const [Dislikes, setDislikes] = useState(0)
    const [DislikeAction, setDislikeAction] = useState(null)

    let variable = {}

    // >   video 
    if (props.video) {
        variable = { videoId: props.videoId, userId: props.userId }
    }
    else {
        variable = { commentId: props.commentId, userId: props.userId }
    }

    useEffect(() => {
        //  
        Axios.post('/api/video/like/getLiked', variable).then(res => {
            if (res.data.success) {
                console.log(res.data)
                //
                setLikes(res.data.likes.length);
                // 
                res.data.likes.map(like => {
                    if (like.userId === props.userId) {
                        setLikeAction('liked')
                    }
                })
            } else {
                alert('   .')
            }
        })

        //   
        Axios.post('/api/video/like/getDisliked', variable).then(res => {
            if (res.data.success) {
                console.log(res.data)
                //
                setDislikes(res.data.dislikes.length);
                // 
                res.data.dislikes.map(dislike => {
                    if (dislike.userId === props.userId) {
                        setDislikeAction('disliked')
                    }
                })
            } else {
                alert('   .')
            }
        })

    }, [])


    const onLike = () => {
        // 
        if (LikeAction === null) {
            Axios.post('/api/video/like/upLike', variable).then(res => {
                if (res.data.success) {
                    setLikes(Likes + 1)
                    setLikeAction('liked')
                    //      .
                    if (DislikeAction !== null) {
                        setDislikeAction(null)
                        setDislikes(Dislikes - 1)
                    }
                }
                else {
                    alert(' ')
                }

            })
        } else { //  
            Axios.post('/api/video/like/unLike', variable).then(res => {
                if (res.data.success) {
                    setLikes(Likes - 1)
                    setLikeAction(null)
                } else {
                    alert('  ')
                }
            })

        }
    }



    const onDislike = () => {
        // 
        if (DislikeAction === null) {
            Axios.post('/api/video/like/upDislike', variable).then(res => {
                if (res.data.success) {
                    setDislikes(Dislikes + 1)
                    setDislikeAction('disliked')
                    //      .
                    if (LikeAction !== null) {
                        setLikeAction(null)
                        setLikes(Likes - 1)
                    }
                }
                else {
                    alert(' ')
                }

            })
        } else { //  
            Axios.post('/api/video/like/unDislike', variable).then(res => {
                if (res.data.success) {
                    setDislikes(Dislikes - 1)
                    setDislikeAction(null)
                } else {
                    alert('  ')
                }
            })

        }
    }




    return (
        <div>
            <span key="comment-basic-like">
                <Tooltip title="Like">
                    <Icon type="like"
                        theme={LikeAction === 'liked' ? "filled" : "outlined"}
                        onClick={onLike}
                    />
                </Tooltip>
                <span style={{ paddingLeft: '8px', cursor: 'auto' }}> {Likes} </span>
            </span>

            <span key="commet-basic-dislike">
                <Tooltip title="Dislike">
                    <Icon type="dislike"
                        theme={DislikeAction === 'disliked' ? "filled" : "outlined"}
                        onClick={onDislike}
                    />
                </Tooltip>
                <span style={{ paddingLeft: '8px', cursor: 'auto' }}> {Dislikes} </span>
            </span>
        </div>
    )
}

export default LikeDislikes